// Основная функция, которая запускает обработчики сбытий.

function main() {
    let textarea = document.getElementsByTagName('textarea')[0];
    textarea.value = localStorage.getItem("text");
    let but_save = document.getElementsByTagName('input')[0];
    let but_clean = document.getElementsByTagName('input')[1];
    let param_for_save = [but_save, "click", save];
    let param_for_clean = [but_clean, "click", clean];
    let param_for_unload = [window, "unload", unload];
    addHandler(...param_for_save);
    addHandler(...param_for_clean);
    addHandler(...param_for_unload);
}

function save() {
    event.preventDefault();
    let textarea = document.getElementsByTagName('textarea')[0];
    let value = textarea.value;
    localStorage.setItem("text", value);
}

function clean() {
    event.preventDefault();
    let textarea = document.getElementsByTagName('textarea')[0];
    textarea.value = "";
}

// Эта функция запускается перед закрытием окна для удаления всех созданных подписок на события.
function unload(){
    //noinspection JSUnresolvedVariable
    removeHandler(textarea, 'focus', entry);
    removeHandler(window, 'unload', unload);
}
